ul {
    width: 100%;
    padding: 0;
    list-style: none;

}

.task {
    background-color: #00000044;
    border-radius: 12px;
    padding: 10px;
    margin: 1.2rem;
}

.task-done {
    margin-right: 2rem;
    width: 5%;
}

.task-description, .task-header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.delete-btn {
    padding: 1rem .1rem;
    width: 20%;
    font-size: 1rem;
}


.done {
    background-color: green;
}