body {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    box-sizing: border-box;
}

.container {
    width: 40vw;
    background-color: #0000006d;
    padding: 1rem 2rem 2rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

button {
    background-color: beige;
    padding: 20px;
    font-size: 1.5rem;
    font-family: 'Courier New', Courier, monospace;
}

input, button {
    box-sizing: border-box;
    border: none;
    width: 100%;
    margin: 0.5rem;
    border-radius: 12px;
}

@media screen and (max-width: 768px) {

    body {
        margin: 0;
        padding: 0;
    }

    .container {
        width: 100vw;
        height: 100vh;
    }
}


